
.vux-x-hr {
  padding-top: 15px;
  margin-bottom: 15px;
  position: relative;
}
.vux-x-hr:before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #C7C7C7;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}



