
.quick-nav{
    margin-bottom: 10px;
}
.quick-nav .item {
    width: 100%;
    height: 60px;
    text-align: center;
    padding-top: 10px;
    font-size: 14px;
    width: calc(100% / 5);
    display: inline-block;
    position: relative;
}
.quick-nav .item span {
    bottom: 0px;
    display: block;
    width: 100%;
    margin-left: -8%;
}
a{
  color: black;
}
.news{
  display: inline-block;
  width: 95%;
  height: 30px;
  margin: 0 6px;
  padding: 0 10px;
  background-color: white;
  border-radius: 10px;
  font-size: 14px;
}
.news img{
  margin-top: 7px;
}
.jd-news{
    width: 73px;
    height: 16px;
    position: relative;
    float: left;
}
.text-scroll{
    color: black;
}
.more{
    float: right;
    margin-right: 15px;
}



